    margin: 1px;
    border-radius: 100%;
}

.b {
    background-color: black;
}

.e {
    background-color: #00000000;
    border: 0px solid black;
}

.w {
    background-color: white;
}

.possible-move-white {
    background-color: #004D1A;
}

.possible-move-black {
