table, td {
    border: 2px solid black;
    border-collapse: collapse;
    padding: 0px;
    margin: 0px;
}

.board {
    background-color: #006622;
    width: 100%;
    padding-top: 100%;
}

table {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%
}
